.input {
    cursor: pointer;
    user-select: none;
    position: relative;
    //width: calc(100% - 12px);
    width: 100%;
    height: 50px;
    padding-left: 10px;
    border-top: none;
    border-left: none;
    border-right: none;
    border-bottom: none;
    outline: none;
    color: gray;
    border-bottom: 1px solid $borderColor;

    @media only screen and (max-width: $smallDisplay) {
        font-size: 12px;
    }
}


