@import "../../../styles/variables";
@import "../../../styles/variables";

.profileWrapper {
  width: 100%;
  height: auto;
  padding: 50px;
  display: flex;
  //border: 1px solid red;
  align-items: center;
  justify-content: center;



}

.profileContainer{
  width: 80%;
  height: auto;
  //border: 1px solid red;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.54);
  border-radius: 14px;
  display: flex;
  padding: 0 5%;

  @media screen and (max-width: 1500px){
    flex-direction: column;
  }

  @media screen and (max-width: 1024px){
    width: 100% ;
  }
}

.profileImage{
  width: 30%;
  height: 300px;
  border: inherit;
  display: flex;
  //align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 50px;
  padding-left: 50px;
  padding-top: 70px;
  @media screen and (max-width: 1500px){
    width: 100%;
    align-items: center;
    padding: 0;
  }
}

.profileInfo{
  width: 70%;
  height: auto;
  //border: 1px solid red;
  padding-top: 70px;
  padding-bottom: 70px;
  //display: flex;
  //flex-direction: column;
  gap: 50px;
  display: grid;
  padding-left: 5%;
  grid-template-columns: repeat(2, 1fr);
  @media screen and (max-width: 1500px){
    width: 100%;
    padding-left: 15%;
  }
}

.userData{
  display: flex;
  flex-direction: column;
  gap: 10px;

  .title{
    font-size: 22px;
  }

  .value{
    font-size: 20px;
    color: rgba(0, 0, 0, 0.61);
  }
  .role{
    font-size: 30px;
  }

}



.buttonsBox{
  width: 100%;
  height: auto;
  display: flex;
  gap: 24px;
  align-items: flex-end;
  justify-content: flex-start;

  & > button{
    width: 150px;
    height: 50px;
    background-color: $asteriskColor;
    border: none;
    border-radius: 25px;
    color: #fff;
    cursor: pointer;
  }


}


.modalBody{
  width: 500px;
  height: auto;
  position: relative;
  background-color: #fff;
  display: flex;

  left: 40%;
  top: 10%;

  flex-direction: column;
  justify-content: flex-start;
}

.modalTop{
  width: 100%;
  height: 70px;
  background-color: $asteriskColor;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-right: 20px;
}

.closeButton{
  width: 32px;
  height: 32px;
  background-color: #fff;
  border-radius: 100%;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.passwordsContainer{
  width: 100%;
  height: calc(100% - 70px);
  padding: 10%;
  display: flex;
  flex-direction: column;
  gap: 40px;
}

.changePasswordEnd{
  //width: 50%;
  //height: 80px;
  background-color: $asteriskColor;
  border: none;
  border-radius: 28px;
  color: #fff;
  cursor: pointer;
  padding: 20px;
}

.text{
  text-align: center;

}

