.existingImageBlock {
    width: 300px;
    margin-top: 20px;
}
.existingImage {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 5px;
}

.error {
    margin-bottom: 10px;
    color: #8B0000;
}

.wrapper {
    * {
        //border: 1px solid black;
        //border-color: $borderColor;
    }
}

.wrapper {
    width: 100%;
    height: 85px;
    display: flex;
    justify-content: space-between;
    column-gap: 10px;
    //gap: 20px;
    //border: 0.1px solid black;
    border-bottom: 1px solid $borderColor;
    padding: 1px 1px 10px 1px;
}

.uploadButtonWrapper {
    width: 100%;
    min-height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    //border-right: 1px solid black;
}

.uploadButtonLabel {
    color: $placeholderColor;
}

.errorInput {

}

.uploadFileText {
    & > svg {
        transform: translateY(0);
        transition: 0.2s;
    }

    &:hover > svg {
        transform: translateY(-5px);
    }
}

.uploadButton {
    //border: 0.1px solid gray;
    //border-right: 1px solid black;

    & > label {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        gap: 20px;
        cursor: pointer;
        color: gray;

        & > input {
            display: none;
        }
    }
}

.filename {
    text-overflow: ellipsis;
    overflow: hidden;
    max-width: 60%;
    width: 60%;
}

.splitter {
    width: 1px;
    height: 100%;
    background-color: black;
}

.datePickerWrapper {
    border-left: 1px solid black;
    padding-left: 20px;
    width: 50%;
    min-width: 50%;
}

.datePickerLabel {
    color: $borderColor;
}

.error {

}
